import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: () => import('@/pages/main/Main'), // 异步组件
    redirect: '/home', // 设置默认重定向到子路由
    //配置子路由
    children: [
      {
        path:'home',
        name:'home',
        component: () => import('@/pages/home/Home')
      },
      {
        path:'mine',
        name:'mine',
        component: () => import('@/pages/mine/Mine')
      }
    ]
  },
  {
    path: '/detail/:id',
    name: 'detail',
    component: () => import('@/pages/detail/Detail') // 异步组件
  },
  {
    path: '/history',
    name: 'history',
    component: () => import('@/pages/mine/History') // 异步组件
  },
  {
    path: '/like',
    name: 'like',
    component: () => import('@/pages/mine/Like') // 异步组件
  },
  {
    path: '/search',
    name: 'search',
    component: () => import('@/pages/home/Search') // 异步组件
  },
  {
    path: '/topic/:id/:title',
    name: 'topic',
    component: () => import('@/pages/home/Topic') // 异步组件
  }
]

const router = new VueRouter({
  routes
})

export default router
