import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultIndex = 0
try {
  // 本地存储
  if (localStorage.tabIndex) {
    defaultIndex = localStorage.tabIndex
  }
} catch (e) {}

export default new Vuex.Store({
  //应用状态的数据结构
  state: {
    tabIndex: defaultIndex,
    playUrl:''
  },
  //允许组件从 Store 中获取状态\store 的计算属性
  getters: {
  },
  //唯一允许更新应用状态的方法（同步操作）
  mutations: {
    changeTabIndex (state, tabIndex) {
      state.tabIndex = tabIndex
      try {
        // 本地存储
        localStorage.tabIndex = tabIndex
      } catch (e) {
      }
    },
    changePlayUrl (state, playUrl) {
      state.playUrl = playUrl
    }
  },
  //可以包含任意异步操作
  actions: {
    changeTabIndex (ctx, tabIndex) {
      // console.log(tabIndex)
      // ctx.commit('changeTabIndex', tabIndex)
    }
  },
  //允许将 store 分割成模块
  modules: {
  }
})
