// utils.js
class Utils {
    constructor() {
      this.updateScreenSize() // 首次调用以设置初始值
      // 绑定事件监听器，当窗口大小改变时更新屏幕尺寸
      this.espMap = new Map() // code:[{}
      this.espIndex = 0
      this.sourceIndex = 0
      this.playTitle = ''
      this.app = 'zcgh'
      this.config = {
        gw: '',
        shareText:'',
        adShow:1,
        adTime:5
      } // gw shareText adShow adTime 
      window.addEventListener('resize', this.updateScreenSize.bind(this))
    }
  
    updateScreenSize() {
      this.screenWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
      this.screenHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
      this.vodCoverWidth = (this.screenWidth / 50 - 0.03*2 - 0.2*2) / 3
      this.vodCoverHeight = this.vodCoverWidth / 0.75
      this.vodlistWidth = (this.screenWidth / 50 - 0.2*2 - 0.2*2) / 3
      this.vodlistHeight = this.vodlistWidth / 0.75
    }

    getNowDateString() {
      // 创建一个新的日期对象，它将包含当前日期和时间
      const now = new Date()
      // 使用Intl.DateTimeFormat来格式化日期
      const formatter = new Intl.DateTimeFormat('en', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
      // 获取格式化的日期部分
      const [{ value: month },,{ value: day },,{ value: year }] = formatter.formatToParts(now)
      // 构造并返回符合 "yyyy-MM-dd" 格式的字符串
      return `${year}-${month}-${day}`
    }

    formatTimeSeconds(secondsNum) {
      // 将总秒数转换为小时、分钟和秒
      let hours = Math.floor(secondsNum / 3600) // 1小时 = 3600秒
      let minutes = Math.floor((secondsNum % 3600) / 60) // 1分钟 = 60秒
      let seconds = Math.floor(secondsNum % 60)

      // 分钟和秒转换为两位数形式
      let minutesStr = minutes.toString().padStart(2, '0')
      let secondsStr = seconds.toString().padStart(2, '0')

      if (hours > 0) {
        // 将小时转换为两位数形式
        let hoursStr = hours.toString().padStart(2, '0')
        // 返回格式化的时间字符串，包括小时部分
        return `${hoursStr}:${minutesStr}:${secondsStr}`
      } else {
        // 仅返回分钟和秒
        return `${minutesStr}:${secondsStr}`
      }
    }

    chunkArray(array, chunkSize) {
      let result = [];
      for (let i = 0; i < array.length; i += chunkSize) {
        result.push(array.slice(i, i + chunkSize))
      }
      return result
    }
}
  
const utils = new Utils()
export default utils
  